.works {
  /*grid-area: w;*/
  /*grid-gap: 10px;*/
  /*margin: 10px 10% 10px 10%;*/
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 24px;
  height: 100vh;
  flex-wrap: wrap;
}
.container {
  /*grid-gap: 5px;*/
  /*margin: 10px 10% 10px 10%;*/
  /*width: 80%;*/
  width: 80%;
  height: 80%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  font-size: 24px;
  color: lightgreen;
  border: 1px solid;
  background-color: rgb(35%, 35%, 35%);
  flex-wrap: wrap;
}

.wrap {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  /*margin: 10px;*/
  background-color: darkgray;
  border: 1px solid;
  height: 90%;
  width: 100%;
  flex-wrap: wrap;
}

.block {
  display: flex;
  flex-direction: column;
  height: 90%;
  width: 40%;
  font-size: 12px;
  align-items: center;
  justify-content: flex-end;
  text-align: center;
  margin: 20px;
  background-color: grey;
  /*color: cyan;*/
  /*border: 1px solid;*/
}

.cube {
  display: flex;
  height: 40%;
  width: 100%;
  text-align: center;
  flex-direction: column;
  /*border-radius: 50%;*/
  margin: 5px;
  background-color: lightgray;
  color: white;
  justify-content: center;
  align-items: center;
  /*order: 4;*/
  font-weight: bold;
}

.img {
  display: flex;
  height: 10%;
  width: 30%;
  text-align: center;
  /*border-radius: 50%;*/
  background-color: dimgrey;
  color: white;
  justify-content: center;
  align-items: center;
  /*order: 1;*/
  align-self: center;
  margin: auto;
}

.description {
  /*display: flex;*/
  height: 50px;
  width: 200px;
  text-align: center;
  /*margin: 5px;*/
  background-color: grey;
  color: white;
  justify-content: center;
  align-items: center;
  /*border: 1px solid;*/
}